.contacts {
  padding: 60px 120px;
  border-top: 1px solid #74819C
}

.contacts h2 {
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 0.03em;
  color: $color-sapphire;
  text-align: left;
  margin: 0;
  margin-bottom: 40px;
}

.contacts__wrapper {
  color: $color-sapphire;
  display: flex;
  flex-direction: row;
  gap: 60px;
}

.contacts__wrapper ul{
  list-style: none;
  font-family: $ff-roboto-condensed;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin: 0;
  position: relative;
}

.contacts__wrapper h3{
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  margin: 0;
  margin-bottom: 5px;
}

.contacts__wrapper p{
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  margin: 0;
}

.contacts__wrapper ul:first-child::after{
  content: "";
  position: absolute;
  height: 117px;
  width: 1px;
  background-color: #74819C;
  right: -52px;
  top: -1px;
}
